html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: 'Cinzel';
  src: url("../fonts/Cinzel-Regular.ttf") format("truetype");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: 'GT Light';
  src: url("../fonts/GTSuperDisplay-Light.ttf") format("truetype");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: 'GT Medium';
  src: url("../fonts/GTSuperDisplay-Medium.ttf") format("truetype");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: 'Avenir Regular';
  src: url("../fonts/AvenirNextCyr-Regular.ttf") format("truetype");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: 'Avenir Medium';
  src: url("../fonts/AvenirNextCyr-Medium.ttf") format("truetype");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: none;
}

body {
  background: #FFf;
  line-height: 1.4;
}

*::selection {
  background: #19A8D5;
  color: white;
}

body {
  font-family: 'Avenir Regular', sans-serif;
  font-weight: 400;
  font-size: 20px;
  width: 100%;
  color: #54555C;
  line-height: 1.7em;
}

h1 {
  font-family: 'Cinzel', serif;
  font-weight: 400;
  font-size: 136px;
  line-height: 1.1em;
  color: #292B33;
}

.header {
  width: 100%;
  position: relative;
}

.header__content {
  height: 68vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 267px;
  padding-right: 267px;
  position: relative;
}

.header__content__description {
  font-size: 24px;
  margin-top: 45px;
}

.header__video {
  width: 100%;
}

.header__video video {
  width: 100%;
}

.nav {
  display: flex;
  justify-content: space-between;
  position: absolute;
  z-index: 100;
  top: 4%;
  width: 100%;
  padding-left: 267px;
  padding-right: 267px;
}

.nav__items {
  display: flex;
}

.nav__items__wrapper {
  display: flex;
  justify-content: space-between;
  align-self: flex-end;
}

.nav__item {
  margin-left: 60px;
}

.nav__item a {
  color: #292B33;
  transition: color .5s ease;
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
}

.nav__item a::after {
  content: '';
  display: block;
  width: 0px;
  height: 2px;
  background: #292B33;
  position: relative;
  transition: width .5s ease, background .5s ease;
  top: -2px;
  align-self: flex-start;
}

.nav__item a:hover {
  color: #19A8D5;
}

.nav__item a:hover:after {
  width: 50%;
  background: #19A8D5;
}

.button {
  padding: 15px 40px;
  border: solid 1px white;
}

.info-block {
  background: #292B33;
  position: absolute;
  bottom: 0;
  right: 0;
  color: white;
  padding: 54px 110px;
}

.info-block__text {
  font-size: 18px;
}

.section {
  position: relative;
  width: 100%;
}

.section__container {
  padding-left: 267px;
  padding-right: 267px;
  padding-top: 160px;
  width: 100%;
  overflow: hidden;
}

.section__container.last {
  padding-bottom: 160px;
}

h2 {
  font-family: 'GT Light', serif;
  font-weight: 300;
  font-size: 60px;
  line-height: 1.1em;
  color: #292B33;
}

.top-content {
  width: 31%;
}

.top-content p {
  margin-top: 25px;
}

.bottom-content {
  width: 100%;
  position: relative;
  margin-top: 80px;
}

.bottom-content img {
  position: relative;
}

.bottom-content img:nth-child(2) {
  position: absolute;
  right: 0;
  top: -80%;
}

.bottom-content.bottom-content__marign {
  margin-top: 40px;
}

h3 {
  font-family: 'GT Medium', serif;
  font-weight: 500;
}

.choose-items {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-basis: 30%;
  flex-wrap: wrap;
}

.choose-items__item {
  flex-basis: 30%;
  margin-top: 45px;
  margin-right: 2.34vw;
}

.choose-items__item p, .choose-items__item h3 {
  margin-top: 10px;
}

.footer {
  background: #292B33;
}

.footer h2 {
  color: white;
}

.footer p {
  color: white;
  margin-top: 20px;
}

.footer__container {
  padding-top: 100px;
  padding-bottom: 100px;
}

.main-footer__content {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.main-footer__content__left-content {
  width: 44%;
}

.main-footer__content__left-content__contacts-items {
  margin-top: 45px;
}

.main-footer__content__left-content__contacts-items a {
  display: block;
  color: white;
  margin-top: 25px;
}

.main-footer__content__right-content {
  width: 40%;
}

.form {
  padding: 1px 0px;
  display: flex;
  flex-direction: column;
  width: 100%;
  color: white;
}

.form .button {
  align-self: flex-end;
  padding: 0px 0px;
  margin-top: 60px;
}

.input-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-top: 60px;
}

.input-wrapper.first {
  margin-top: 0px;
}

.input-wrapper input {
  font-size: 16px;
  outline: none;
  border: 0;
  padding-top: 20px;
  padding-bottom: 15px;
  background: transparent;
  color: white;
  font-family: 'Avenir Regular', sans-serif;
  font-weight: 400;
}

.input-wrapper input:focus {
  border: none;
  color: white;
}

.input-wrapper label {
  position: absolute;
  font-family: 'GT Medium', serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1em;
  bottom: 0px;
  left: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.input-wrapper label::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -1px;
  height: 100%;
  width: 100%;
  border-bottom: 2px solid;
  border-color: white;
  width: 0px;
  transition: width 0.5s ease, border-color .5s ease;
  align-self: center;
  justify-self: center;
}

.label-content {
  position: absolute;
  font-size: 16px;
  color: white;
  bottom: 11px;
  left: 0;
  opacity: 1;
  transition: color 0.4s ease, transform 0.4s ease, font-size 0.4s ease;
}

.focus + .label .label-content {
  transform: translateY(-35px);
  font-size: 14px;
  opacity: 1;
  color: #19A8D5;
}

.focus + .label:after {
  width: 100%;
}

.form-button {
  border: none;
  background: transparent;
  color: inherit;
  cursor: pointer;
  outline: none;
  font-size: 17px;
  overflow: hidden;
  position: relative;
  z-index: 2;
  width: 100%;
  height: 100%;
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
  line-height: 1em;
  padding: 15px 80px;
}

.form-validate-text {
  color: #F44336;
  display: block;
  margin-top: 5px;
  font-size: 14px;
  position: absolute;
  transition: opacity .3s ease;
  opacity: 0;
  bottom: -27px;
}

.strip-outer {
  width: 100vw;
  margin: auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: none;
}

.strip-inner {
  position: relative;
  top: 0;
  height: 100%;
  width: -moz-fit-content;
  width: fit-content;
  overflow-x: hidden;
}

.draggable {
  height: 400px;
  top: calc(50% - 250px);
  width: 100%;
  position: absolute;
  cursor: move;
}

.strip {
  pointer-events: none;
  height: 100%;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  align-items: start;
  position: relative;
  margin-right: 200px;
}

.cursor {
  display: none;
}

.strip__item {
  height: 100%;
  position: relative;
  padding-right: 55px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.strip__item.last__item {
  padding-right: 256px;
}

.strip__item-name {
  color: #292B33;
  font-family: 'GT Medium', serif;
  font-weight: 500;
  user-select: none;
  line-height: 1em;
}

.strip__item-name span {
  overflow: hidden;
  display: block;
}

.img-outer {
  height: 551px;
  width: 584px;
  position: relative;
  flex: none;
}

.img-inner {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  opacity: 0;
  pointer-events: none;
}

.img-inner.current {
  overflow: initial;
  opacity: 1;
  pointer-events: auto;
}

.img-inner-2 {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-position: 50% 50%;
}

.strip-after {
  margin-top: 30px;
  width: 100%;
}

.strip-after p {
  margin-top: 10px;
}

.strip-link {
  color: #292B33;
  font-size: 20px;
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
  pointer-events: auto;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  line-height: 1em;
  transition: color .5s ease;
  margin-right: 2px;
  width: fit-content;
  margin-top: 15px;
  margin-bottom: 10px;
}

.strip-link::after {
  content: '';
  display: block;
  width: 50%;
  height: 2px;
  background: #292B33;
  position: relative;
  transition: width .5s ease, background .5s ease;
  top: 4px;
  align-self: flex-start;
}

.strip-link:hover {
  color: #19A8D5;
}

.strip-link:hover:after {
  width: 0;
  background: #19A8D5;
}

.strip,
.strip__item {
  will-change: transform;
}

.item__wrapper {
  overflow: hidden;
}

.strip-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  left: -114px;
  top: 40%;
  transform: translate(-50%, -50%);
}

.strip-nav__left, .strip-nav__right {
  cursor: pointer;
  opacity: 0.8;
  transition: opacity .3s ease;
}

.strip-nav__left:hover, .strip-nav__right:hover {
  opacity: 1;
}

.strip-nav__left {
  margin-right: 50px;
}

.waves-wrapper {
  width: 100vw;
  position: absolute;
  bottom: -20vh;
  z-index: -1;
  left: -267px;
}

.waves-wrapper svg {
  width: 100%;
}

.wave {
  animation: wave 3s linear;
  animation-iteration-count: infinite;
  fill: none;
  stroke: #19A8D5;
  stroke-width: 0.15px;
}

#wave2 {
  animation-duration: 5s;
  animation-direction: reverse;
  opacity: .6;
}

#wave3 {
  animation-duration: 7s;
  opacity: .3;
}

@keyframes wave {
  to {
    transform: translateX(-100%);
  }
}
